import { useContext } from "react";
import { MapContext, SearchIcon, ArrowDown } from "../shared";

const Search = () => {
  const {
    isKeyboardTyping,
    selectedBlockOption,
    handleBlockOptionChange,
    search,
    handleSearchInput,
  } = useContext(MapContext);

  if ("virtualKeyboard" in navigator) {
    navigator.virtualKeyboard.overlaysContent = true;
  }

  return (
    <div className="search">
      <div className="chakra-select__wrapper">
        <select
          id="search__select-block"
          title="select block"
          className="chakra-select"
          aria-label="select block"
          disabled={isKeyboardTyping}
          value={selectedBlockOption}
          onChange={handleBlockOptionChange}
        >
          <option value="">All</option>
          <option value="B1">C1.1</option>
          <option value="B2">C1.2</option>
          <option value="B3">C1.3</option>
        </select>
        <div className="chakra-select__icon-wrapper">
          <ArrowDown />
        </div>
      </div>
      <div className="chakra-input__group">
        <input
          id="search__chakra-input"
          title="search room input field"
          className="chakra-input"
          type="text"
          placeholder="C1.2.223 or 2.223"
          value={search}
          onChange={(e) => handleSearchInput(e)}
        />
        <div className="chakra-input__right-element">
          <SearchIcon />
        </div>
      </div>
    </div>
  );
};

export default Search;
