import MapProvider from "./MapProvider";
import { RenderMap } from "../page";
import { Drawer, Search, FloorOption } from "../components";
import { useKeyboardAvoid } from "../shared";

function App() {
  useKeyboardAvoid();

  return (
    <MapProvider>
      <div className="app__box">
        <FloorOption />
        <Search />
        <Drawer />
        <RenderMap />
      </div>
    </MapProvider>
  );
}

export default App;
