import { useTheme, DoubleArrowLeft, DoubleArrowRight } from "../shared";

const Drawer = () => {
  const { toggleTheme, toggleFancyMode, fancy } = useTheme();

  return (
    <>
      <input title="drawer-state" type="checkbox" id="drawer-state" />
      <label
        id="drawer-state__open"
        htmlFor="drawer-state"
        className="chakra-button drawer__open-button"
      >
        <DoubleArrowRight />
      </label>
      <div className="drawer__overlay" />
      <div className="drawer">
        <div className="drawer__header">
          <div className="drawer__header__title">aitu map</div>
          <label
            id="drawer-state__close"
            htmlFor="drawer-state"
            className="chakra-button drawer__close-button"
          >
            <DoubleArrowLeft />
          </label>
        </div>
        <div className="drawer__body">
          <button className="chakra-button" onClick={toggleTheme}>
            change theme
          </button>
          <a href="https://github.com/Yuujiso/aitumap">
            <button className="chakra-button">github</button>
          </a>
          <a href="https://t.me/yuujisoo/74">
            <button className="chakra-button">about</button>
          </a>
          <div className="fancy-toggle">
            <samp>fancy mode beta</samp>
            <input
              id="fancy-toggle-state"
              title="toggle fancy mode (high CPU usage)"
              checked={fancy}
              onChange={toggleFancyMode}
              type="checkbox"
              className="fancy-toggle__switch"
            />
          </div>
        </div>
        <div className="drawer__footer">
          <a href="https://t.me/yuujisoo">
            <samp>{"Developer :: @yuujisoo"}</samp>
          </a>
          <a href="https://t.me/meeezzanine">
            <samp>{"Designer :: @meeezzanine"}</samp>
          </a>
          <a href="https://t.me/yuujiso" className="drawer__footer_mt_10">
            <samp>{"Report a problem/misleading information"}</samp>
          </a>
        </div>
      </div>
    </>
  );
};

export default Drawer;
