import { useEffect, useState } from "react";

const useTheme = () => {
  const getPreferredTheme = () =>
    window.matchMedia &&
    window.matchMedia("(prefers-color-scheme: dark)").matches
      ? "dark"
      : "light";

  const [theme, setTheme] = useState(
    localStorage.getItem("theme") || getPreferredTheme()
  );

  const [fancy, setFancy] = useState(localStorage.getItem("fancy") === true);

  useEffect(() => {
    localStorage.setItem("theme", theme);
    localStorage.setItem("fancy", fancy);

    const root = document.documentElement;
    root.className = theme;
    root.style.colorScheme = theme;
    root.setAttribute("data-theme", theme);
    root.setAttribute("data-fancy", fancy.toString());
  }, [theme, fancy]);

  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === "light" ? "dark" : "light"));
  };

  const toggleFancyMode = () => {
    setFancy((prevFancy) => !prevFancy);
  };

  return { theme, toggleTheme, toggleFancyMode, fancy };
};

export { useTheme };
